@use'./custom' as * ;

.benefits {
  margin-top: 5%;
  margin-bottom: 20px;
  .benefits__content{
    article{
      
      font-weight:bold
    }
    font-size: 1rem;
    color: $main-darkest;
  }
  @media (max-width: 768px) {
    .benefits__content{
      article{
        
        font-weight:bold
      }
      font-size: 4dvw;
    }
  }
  .benefits_info {
    display: flex;
    flex-direction: column; 
    align-items: center;
    justify-content: center;
    text-align: center; s

    .benefits__image {
      display: none;
      box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
      margin-bottom: 20px; 
    }

    @media (min-width: 768px) { 
      flex-direction: row; 
      .benefits__image {
        width: 40%;
        margin-bottom: 0; 
      }
    }
  }
}