@use './custom' as * ;

.register-page {
    display: flex; 
    align-items: center;
    justify-content: center;
    min-height: 100vh; 
  }
  
  .register-form {
    flex: 1; /* Allow form to fill available space */
    max-width: 400px; /* Max width for the form */
    padding: 2rem;
    border: 1px solid #ddd;
    align-items: center;
    justify-content: center;
  border-radius: 15px;
    border: 3px solid $light;
  .form--Buttons{
    display: flex;
    align-items: center;
    justify-content: center;
  }
    .fullName{
        display: flex;
    }
  }
  
  .register-form h2 {
    margin-bottom: 1rem;
    font-size: 1.5rem;
    text-align: center; /* Center the heading */
  }
  
  .register-form input {
    width: 100%;
    padding: 0.5rem;
    margin-bottom: 1rem; /* Space between inputs */
    border: 1px solid #ccc;
    border-radius: 15px;
    box-sizing: border-box; /* Ensure padding is included in total width */
  }
  
  
  .register-form button:hover {
    background-color: #0056b3; /* Darker shade on hover */
  }
  
  .register-image {
    width: 40%; /* Adjust image width */
    max-width: 600px; /* Ensure image does not get too large */
  }
  
  .register-image img {
    width: 100%;
    border-radius: 5px; /* Rounded corners for the image */
  }
  