.collab-card{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    width: auto;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1), 0 6px 20px 0 rgba(0, 0, 0, 0.10);
    transition: 0.3s;
    margin-bottom: 10px;

    &:hover{
        box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.3), 0 12px 40px 0 rgba(0, 0, 0, 0.19);
        transform: translateY(-5px);
    }

    .growth--icon{
        width: 20px; 
        height: 20px; 
        margin-right: 0.5rem;
        // margin-bottom: 0;
    }

    p{
        margin: 5px;
        font-weight: bold;
    }

}