.resource-menu {
  position: relative;
  display: inline-block;
  cursor: pointer;

  .menu {
    display: none;
    position: absolute;
    right: 0;
    background-color: #ffffff;
    border: 1px solid #cccccc;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    z-index: 100;
    min-width: 160px;
    margin-top: 5px;
    max-height: 120px;
    height: fit-content;

    li {
      padding: 10px 15px;
      color: #333333;
      font-size: 14px;
      display: flex;
      align-items: center;

      svg {
        margin-right: 8px;
      }

      &:hover {
        background-color: #e82a2a;
        cursor: pointer;
      }
    }
  }

  .menu, &:focus-within .menu {
    display: block;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}