body{
    ::-webkit-scrollbar {
        width: 10px;
        margin-right: 5px !important;

    }

    a{
        text-decoration: none;
        color: inherit;
        cursor: inherit;
    }
    
    ::-webkit-scrollbar-track {
        border-radius: 8px;
        background-color: #e7e7e7;
    }
    
    ::-webkit-scrollbar-thumb {
        border-radius: 8px;
      border: 3px solid transparent;
       background-clip: content-box;
        background-color: $main;
    }
    
}

