@use './custom' as * ;

@mixin btn-base {
    display: inline-block;
    padding: 0.5rem 1rem;
    border: 1px solid #ccc;
    
    cursor: pointer;
    text-align: center;
    text-decoration: none; /* Remove underline */
    font-weight: bold; /* Optional */

    &:hover {
        color: $main-darkest;
    }


  }
  
  .btn{
    @include btn-base;
  }
  
  .btn--primary {
    @include btn-base; 
    background-color: $main; 
    color: #fff; 
  }
  
  .btn--secondary {
    @include btn-base;
    background-color: $main-darkest;
    color: #fff;
  }
  
  .btn--outline {
    @include btn-base;
    background-color: transparent;
    border-color: $main; /* Adjust border color as needed */
    color: $main;
  }

  .add-goal{
    button{
      @include btn-base; 
      background-color: $main; 
      color: #fff; 
      border-radius: 10px;
      margin: 0;
      cursor: pointer;
      text-align: center;
      text-decoration: none; 
      font-weight: bold; 
    }
  }
  