/* ------ Global ------ */
* {
	margin: 0;
	padding: 0;
	text-decoration: none;
}

------ Body ------ .container {
}

main {
	width: 100%;
	padding: 20px;
}

.sidebar {
	background-color: #000;
	color: #fff;
	height: 80vh;
	z-index: 200;
	margin-top: 70px;
}

.top-section {
	display: flex;
	align-items: center;
	padding: 15px;
}

.logo {
	font-size: 30px;
}

.bars {
	display: flex;
	font-size: 25px;
	margin-left: 50px;
	/* padding-left: 40px; */
}

.link {
	display: flex;
	color: #fff;
	padding: 10px 20px;
	gap: 16px;
	transition: all 0.5s;
	-webkit-transition: all 0.5s;
	-moz-transition: all 0.5s;
	-ms-transition: all 0.5s;
	-o-transition: all 0.5s;
}

.link:hover {
	background-color: lightskyblue;
	color: #000;
	transition: all 0.5s;
	-webkit-transition: all 0.5s;
	-moz-transition: all 0.5s;
	-ms-transition: all 0.5s;
	-o-transition: all 0.5s;
}
.body-section {
	background-color: red;
	margin-left: 70px;
	width: auto;
}
.active {
	background-color: lightskyblue;
	color: #000;
}

.icondd,
.link-text {
	font-size: 18px;
}

.container {
	position: relative;
	width: 100%;
}

