@media (min-width: 768px) {
	.sidebar {
		position: absolute;
		top: 0;
		left: 0;
		z-index: 1000;
		transition: width 0.3s ease;
		background-color: #000000;
		color: #ff1313;
        display: block!important;
	}
}
